@import 'assets/scss/utils/mixins';

.pagination-item.active {
	z-index: 99;
	background-color: var(--info-1);
	border: 1px solid var(--info-3);
}

.pagination-list {
	list-style-type: none;
	margin: 0;
	padding: 0;
	display: inline-block;
}

.pagination-item {
	width: 40px;
	height: 40px;
	line-height: 40px;
	border-radius: 4px;
	background-color: var(--general-1);
	margin: 4px;
	display: inline-block;
	vertical-align: middle;
	text-align: center;

	&--next {
		margin-inline-start: 12px;
	}

	&--prev {
		margin-inline-end: 12px;
	}
}

.pagination-link {
	color: var(--general-4);
	text-decoration: none;
	font-size: 14px;

	&.active {
		color: var(--info-3) !important;
	}
}

.pagination-link i {
	display: inline-block;
}

.pagination-link:hover {
	color: var(--info-3);
}

.pagination-item.disabled .pagination-link {
	color: var(--general-3);
	opacity: 0.5;
}

.pagination-link:focus {
	box-shadow: none;
}

.pagination-dots {
	background-color: transparent;
	border: none;
	display: inline-block;
}
